import {
  type Location,
  type NavigateFunction,
  useLocation,
  useNavigate,
} from '@remix-run/react';

import {
  type AuthDispatch,
  type AuthState,
  AuthStateLoggedIn,
  useAuth,
} from 'src/code.client/auth/auth_context';
import { useRetryDialog } from '~/dialogs/RetryDialog';
import { type MinimalAppResources, type RetryCallback } from 'src/domain/shared/types';

export interface BasicAppResources extends MinimalAppResources {
  location: Location;
  navigate: NavigateFunction;
  authState?: AuthStateLoggedIn;
  authDispatch?: AuthDispatch;
  retryCallback?: RetryCallback;
}

export function useBasicAppResources(
  authStateOverride?: AuthState,
  authDispatchOverride?: AuthDispatch
): BasicAppResources {
  const location = useLocation();
  const navigate = useNavigate();

  const auth = useAuth();
  const authState = authStateOverride ?? auth.state;
  const authDispatch = authDispatchOverride ?? auth.dispatch;
  const retryCallback = useRetryDialog();

  // const { isOnline } = useClientNetworkStatus();
  const authToken = authState.authTokenString();

  return {
    authToken,
    location,
    navigate,
    ...(authState instanceof AuthStateLoggedIn
      ? { authState, authDispatch }
      : {}),
    retryCallback,
  };
}
