/// **************************************************************************
///
/// ApiRequestState classes
///
import { type AxiosRequestConfig } from 'axios';

import { type DXTErrorInfo } from '../../domain/shared/errors/base';

/// **************************************************************************
type DXTApiRequestStateMapFunction<
  T,
  D,
  S extends DXTApiRequestState<T, D>,
  R,
> = (state: S) => R;

export abstract class DXTApiRequestState<T, D = any> {
  constructor(public readonly originalRequest: AxiosRequestConfig<D>) {}

  map<R>(options: {
    success?: DXTApiRequestStateMapFunction<
      T, D, DXTApiRequestStateSuccess<T, D>, R
    >;
    error?: DXTApiRequestStateMapFunction<
      T, D, DXTApiRequestStateError<T, D>, R
    >;
  }): R {
    const { success, error } = options;

    if (this instanceof DXTApiRequestStateSuccess && success) return success(this);

    if (this instanceof DXTApiRequestStateError && error) return error(this);

    throw Error('Invalid ApiRequestState state');
  }

  public isError(): boolean {
    return this instanceof DXTApiRequestStateError;
  }

  public errorOrNull(): DXTErrorInfo | null {
    if (this instanceof DXTApiRequestStateError) return this.error;
    return null;
  }

  public flat(): T | DXTErrorInfo {
    return this.map<T | DXTErrorInfo>({
      error: (errorState) => errorState.error,
      success: (successState) => successState.data,
    });

  }
}

export class DXTApiRequestStateError<T, D> extends DXTApiRequestState<T, D> {
  constructor(
    public readonly error: DXTErrorInfo,
    originalRequest: AxiosRequestConfig<D>
  ) {
    super(originalRequest);
  }
}

export class DXTApiRequestStateSuccess<T, D> extends DXTApiRequestState<T, D> {
  constructor(
    public readonly data: T,
    originalRequest: AxiosRequestConfig<D>
  ) {
    super(originalRequest);
  }
}
