import axios, { AxiosError, type AxiosRequestConfig, type AxiosResponse } from 'axios';

const IS_CLIENT_ONLINE_CHECK_URL = 'https://one.one.one.one/';

export class AxiosRequestPlusError extends Error {
  name: string;
  stack?: string;

  constructor(
    readonly originalError: any,
    readonly isInternetAlive: boolean,
    message?: string,
  ) {
    const resolvedMessage = message ?? originalError?.message;
    super(typeof resolvedMessage === 'string' ? resolvedMessage : undefined);

    this.name = 'AxiosRequestPlusError';
    this.stack = typeof originalError?.stack === 'string' ? originalError?.stack : undefined;
  }
}

async function _isInternetAlive(): Promise<boolean> {
  try {
    const response = await fetch(IS_CLIENT_ONLINE_CHECK_URL, {
      method: 'HEAD',
    });

    if (response.ok) return true;
    return false;
  } catch (_) {
    return false;
  }
}

export async function axiosRequestPlus<T = any, R = AxiosResponse<T>, D = any>(
  config: AxiosRequestConfig<D>
): Promise<R> {
  try {
    return await axios.request(config);
  } catch (e) {
    let isInternetAlive: boolean = false;

    if (e instanceof AxiosError && e.response) {
      // There's a response. Internet is alive.

      isInternetAlive = true;
    } else {
      try {
        isInternetAlive = await _isInternetAlive();
      } catch (_) {}
    }

    throw new AxiosRequestPlusError(e, isInternetAlive);
  }
}
