import { valueToString } from '../strings';
import { toBoolean } from '../transformation';
import { UINT64_MAX_VALUE, UINT64_MIN_VALUE } from '../validation/base';
import { type EntityId, ValueObjectException } from './types';

/********************************************************************************************
 ********************************************************************************************/


export type VOInput<R> = R | string | undefined | null;

export abstract class ValueObjectBase<R, T = R|string> {
  protected abstract value: R;

  toString(): string {
    if (this.value === undefined) {
      return 'undefined';
    } else if (this.value === null) {
      return 'null';
    } else {
      return (this.value as any).toString();
    }
  }

  valueOf(): R {
    return this.value;
  }

  rawValueToErrorString(input: VOInput<T>): string {
    return valueToString(input);
  }

  extraErrorInfo(input: unknown): string {
    return '';
  }

  getError(
    input: unknown,
    customMessageBegin?: string
  ): ValueObjectException {
    const className = this.constructor.name.toString();

    const rawValueErrorString = '******';

    const extraErrorInfo = this.extraErrorInfo(input);

    const messageBegin = customMessageBegin ?? `Invalid value`;

    const exceptionMessage =
      `${messageBegin} ${className}(${rawValueErrorString})` +
      (extraErrorInfo !== '' ? `. ${extraErrorInfo}` : '');
    return new ValueObjectException(exceptionMessage);
  }

  throwError(input: unknown, customMessage?: string): never {
    throw this.getError(input, customMessage);
  }

  abstract validate(input: VOInput<T>): R;
}


export type ValueObjectR<VO> = VO extends ValueObjectBase<infer R, any> ? R : never;
export type ValueObjectT<VO> = VO extends ValueObjectBase<any, infer T> ? T : never;

export abstract class ValueObject<R, T=R|string> extends ValueObjectBase<R, VOInput<T>> {
  protected value: R;

  constructor(input: VOInput<T>) {
    super();
    this.value = this.validate(input);
  }
}

export abstract class VOEntityId<T> extends ValueObject<
  EntityId, T|EntityId|string
> {}

/********************************************************************************************
 ********************************************************************************************/

export class VONumber extends ValueObject<number> {

  override validate(input: VOInput<number>): number {
    let value = input;

    try {
      if (typeof value === 'string') value = Number.parseFloat(value);
      if (typeof value === 'number' && Number.isFinite(value)) return value;
    } catch (_) {}

    this.throwError(input);
  }

}

/********************************************************************************************
 ********************************************************************************************/

export class VOInteger extends ValueObject<number> {

  override validate(input: VOInput<number>): number {
    let value = input;

    try {
      if (typeof value === 'string') value = Number.parseInt(value);
      if (typeof value === 'number' && Number.isInteger(value)) return value;
    } catch (_) {}

    this.throwError(input);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export class VOBigInt extends ValueObject<bigint, bigint|number> {

  override validate(input: VOInput<bigint|number>): bigint {
    let value = input;

    try {
      if (typeof value === 'string') value = BigInt(value);
      if (typeof value === 'number') value = BigInt(value);
      if (typeof value === 'bigint') return value;
    } catch (e) {}

    this.throwError(input);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export class VONumberRange<R extends number = number> extends ValueObjectBase<R, number | string> {
  protected value: R;

  isInteger: boolean;

  constructor(
    value: VOInput<number>,
    readonly min?: number,
    readonly max?: number,
    isInteger?: boolean
  ) {
    super();
    this.isInteger = isInteger ?? false;
    this.value = this.validate(value);
  }

  override extraErrorInfo(input: VOInput<string>): string {
    return `must to be between ${this.min} and ${this.max}`;
  }

  override validate(input: VOInput<number>): R {
    let value = input;

    try {
      if (typeof value === 'string') value = this.isInteger ? Number.parseInt(value) : Number.parseFloat(value);

      if (
        (value == null)
        ||
        (this.isInteger && !Number.isInteger(value))
        ||
        (!this.isInteger && !Number.isFinite(value))
      ) {
        this.throwError(input);
      }

      if (((this.min == null) || value >= this.min) && ((this.max == null) || value <= this.max)) return value as R;
    } catch (_) {}

    this.throwError(input);
  }
}

export class VOIntegerRange extends VONumberRange<number> {
  constructor(
    value: VOInput<number>,
    min: number | undefined,
    max: number | undefined
  ) {
    super(value, min, max, true);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export class VOPositiveNumber extends VONumberRange<number> {
  constructor(value: VOInput<number>) {
    super(value, 0, undefined, undefined);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export class VOBigIntRange extends ValueObjectBase<
  bigint,
  bigint | number | string
> {
  protected value: bigint;

  constructor(
    value: VOInput<bigint | number>,
    readonly min: bigint | undefined,
    readonly max: bigint | undefined
  ) {
    super();
    this.value = this.validate(value);
  }

  override validate(input: VOInput<bigint | number>): bigint {
    let value = input;

    try {
      if (typeof value === 'string' || typeof value === 'number') value = BigInt(value);

      if (typeof value !== 'bigint') this.throwError(input);

      if (((typeof this.min !== 'bigint') || value >= this.min) && ((typeof this.max !== 'bigint') || value <= this.max)) return value;
    } catch (_) {}

    this.throwError(input);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export class VOPositiveBigInt extends VOBigIntRange {
  constructor(value: VOInput<bigint | number>) {
    super(value, BigInt(0), undefined);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export abstract class VOUInt64 extends VOBigIntRange {
  constructor(value: VOInput<bigint | number>) {
    super(value, UINT64_MIN_VALUE, UINT64_MAX_VALUE);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export class VOBoolean extends ValueObject<boolean, boolean | number | string> {
  override validate(input: VOInput<boolean | number>): boolean {
    try {
      return toBoolean(input);
    } catch (_) {}

    this.throwError(input);
  }
}

/********************************************************************************************
 ********************************************************************************************/

export class VOString extends ValueObject<string> {
  validate(input: VOInput<string>): string {
    if (typeof input !== 'string') this.throwError(input); 
    
    return input;
  }
}
