import { type BasicAppResources } from 'src/code.client/app_resources/basic_app_resources';
import { type DXTApiOkResult } from 'src/api-universal/types';

import {
  type AuthInfoResult,
} from '../../domain/auth/results';

import {
  type LoginUserInput,
} from '../../domain/auth/inputs';

import { dxtApiRequest } from '../shared/dxtApiRequest';
import {
  type DXTApiRequestState,
  DXTApiRequestStateError,
  DXTApiRequestStateSuccess,
} from '../shared/DXTApiRequestState';

import { API_AUTH_CONNECT, API_AUTH_LOGIN, API_AUTH_LOGOUT } from './paths';


export const userAuthConnectRequest = async (
  token: string,
  app: BasicAppResources,
  silent: boolean
) => {
  const originalUserInfo =
    token === app.authState?.authToken ? app.authState?.userInfo : null;

  const user_info_updated_at = originalUserInfo
    ? originalUserInfo.timestamp_modificacion
    : 0;

  const result = await dxtApiRequest<AuthInfoResult>(
    {
      url: API_AUTH_CONNECT,
      method: 'POST',
      headers: { authorization: token },
      params: {
        user_info_updated_at,
      },
      silent,
    },
    app
  );

  return result.map<DXTApiRequestState<AuthInfoResult>>({
    error: (errorState) => {
      return new DXTApiRequestStateError(
        errorState.error,
        errorState.originalRequest
      );
    },
    success: (success) => {
      if (success.data == null && originalUserInfo) {
        return new DXTApiRequestStateSuccess(
          {
            auth_token: token,
            user: originalUserInfo,
          },
          success.originalRequest
        );
      }

      return new DXTApiRequestStateSuccess(
        success.data,
        success.originalRequest
      );
    },
  });
};


export const userLoginRequest = async (
  input: LoginUserInput,
  app: BasicAppResources
) => {
  return await dxtApiRequest<AuthInfoResult, LoginUserInput>(
    {
      url: API_AUTH_LOGIN,
      data: input,
      method: 'POST',
      silent: true
    },
    app
  );
};


export const userLogoutRequest = async (app: BasicAppResources) => {
  return await dxtApiRequest<DXTApiOkResult>(
    {
      url: API_AUTH_LOGOUT,
      method: 'POST',
    },
    app
  );
};
