import { type Dispatch, type SetStateAction } from 'react';

import { type BasicAppResources } from '../../../app_resources/basic_app_resources';

import { type AuthState } from '../states';

export type OnRetryCallBack = () => Promise<boolean>;

export abstract class AuthAction {
  abstract run(
    authState: AuthState,
    setAuthState: Dispatch<SetStateAction<AuthState>>,
    app: BasicAppResources,
    onRetryCallback: OnRetryCallBack | undefined | null
  ): Promise<void>;
}
