import { type Dispatch, type SetStateAction } from 'react';

import { type BasicAppResources } from 'src/code.client/app_resources/basic_app_resources';
import { URL_AUTH_LOGIN_PATH } from 'src/config/url_paths';
import { userLogoutRequest } from 'src/api-client/auth';

import { type AuthState, AuthStateDisconnected, AuthStateLoggedIn } from '../states';
import { AuthAction, type OnRetryCallBack } from './base';

export class AuthActionLogout extends AuthAction {
  readonly avoidCallingAPI: boolean;
  // doNotCallAPI: used when user token has already expired or is invalid
  // one less call to the API since user is in fact unauthorized
  constructor(avoidCallingAPI?: boolean) {
    super();

    this.avoidCallingAPI = avoidCallingAPI ?? false;
  }

  async run(
    authState: AuthState,
    setAuthState: Dispatch<SetStateAction<AuthState>>,
    app: BasicAppResources,
    onRetryCallback: OnRetryCallBack | undefined | null
  ): Promise<void> {
    if (authState instanceof AuthStateLoggedIn && !this.avoidCallingAPI) {
      await userLogoutRequest({
        ...app,
        authState,
      });
    }

    setAuthState(new AuthStateDisconnected(undefined));

    app.navigate(URL_AUTH_LOGIN_PATH);
  }
}
