import { stringifyJSON } from "../json";
import { isObj } from "../objects";

export const isStr = (value: unknown): value is string => typeof value === 'string';

export function linesCount(value: string): number {
  if (typeof value !== 'string') return -1;

  const len = value.length;
  let lines = 0;
  let lastLineBreakPos = -1;
  for (let i = 0; i < len; i++) {
    if (value[i] === '\n') {
      lines++;
      lastLineBreakPos = i;
    }
  }

  if (lastLineBreakPos < len - 1) lines++;

  return lines;
}


export function valueToString(value: any | undefined | null, includeStringQuotes?: boolean): string {
  if (Array.isArray(value) || isObj(value)) return stringifyJSON(value);

  if (typeof value === 'string' && (includeStringQuotes ?? false)) return `'${value}'`;
  
  if (value === null) return '[null]';

  return value?.toString?.() ?? '[undefined]';
}
