export const HTTP_STATUS_INFORMATION_MIN = 100;
export const HTTP_STATUS_INFORMATION_MAX = 199;

export const HTTP_STATUS_SUCCESS_MIN = 200;
export const HTTP_STATUS_SUCCESS_MAX = 299;

export const HTTP_STATUS_REDIRECT_MIN = 300;
export const HTTP_STATUS_REDIRECT_MAX = 399;

export const HTTP_STATUS_CLIENT_ERROR_MIN = 400;
export const HTTP_STATUS_CLIENT_ERROR_MAX = 499;

export const HTTP_STATUS_SERVER_ERROR_MIN = 500;
export const HTTP_STATUS_SERVER_ERROR_MAX = 599;

export function isHttpInformationStatus(status: number): boolean {
  return (
    status >= HTTP_STATUS_INFORMATION_MIN &&
    status <= HTTP_STATUS_INFORMATION_MAX
  );
}

export function isHttpSuccessStatus(status: number): boolean {
  return status >= HTTP_STATUS_SUCCESS_MIN && status <= HTTP_STATUS_SUCCESS_MAX;
}

export function isHttpRedirectStatus(status: number): boolean {
  return (
    status >= HTTP_STATUS_REDIRECT_MIN && status <= HTTP_STATUS_REDIRECT_MAX
  );
}

export function isHttpClientError(status: number): boolean {
  return (
    status >= HTTP_STATUS_CLIENT_ERROR_MIN &&
    status <= HTTP_STATUS_CLIENT_ERROR_MAX
  );
}

export function isHttpServerError(status: number): boolean {
  return (
    status >= HTTP_STATUS_SERVER_ERROR_MIN &&
    status <= HTTP_STATUS_SERVER_ERROR_MAX
  );
}

export function isHttpError(status: number): boolean {
  return isHttpClientError(status) || isHttpServerError(status);
}
