import { HttpException } from '@depsel-nodejs/api-server';
import { AxiosRequestPlusError } from '@/axios/axiosRequestPlus';
import { AxiosError } from 'axios';

import {
  DXTApiMalformedResponse,
  DXTErrorCode,
  type DXTErrorInfo,
  DXTException,
  getDXTErrorDescription,
  getErrorCodeFromHttpStatus,
  getGenericErrorCode,
  getHttpStatusFromErrorCode,
} from './base';

import { ValidationException } from '@/validation/errors';
import { isRealObject } from '@/objects';
import { HttpStatus } from '@/http';


export function isErrorInfo(data: any): boolean {
  const errorInfo: DXTErrorInfo = data;

  if (!isRealObject(errorInfo)) return false;

  const { status, errorCode, error } = errorInfo;

  const statusNumber =
    status && typeof status === 'number'
      ? status
      : Number.parseInt(status?.toString());

  const errorCodeNumber =
    errorCode && typeof errorCode === 'number'
      ? errorCode
      : Number.parseInt(errorCode?.toString());

  return (
    Number.isInteger(statusNumber) &&
    Number.isInteger(errorCodeNumber) &&
    typeof error === 'string'
  );
}


export function exceptionToDXTErrorInfo(error: any): DXTErrorInfo {
  const originalErrorInfo = (error as any)?.originalError?.response?.data;

  if (isErrorInfo(originalErrorInfo)) return originalErrorInfo as DXTErrorInfo;

  if (error instanceof DXTException) return error.dxtError;

  if (error instanceof ValidationException) {
    const statusCode = HttpStatus.BAD_REQUEST;
    const errorCode = getErrorCodeFromHttpStatus(statusCode);

    return {
      status: statusCode,
      error: getDXTErrorDescription(errorCode),
      errorCode,
      extra: error.message
    };

  }


  if (error instanceof HttpException) {
    const { statusCode } = error;

    const errorCode = getErrorCodeFromHttpStatus(statusCode);

    return {
      status: statusCode,
      error: getDXTErrorDescription(errorCode),
      errorCode,
      extra: error.message
    };
  }


  let isInternetAlive: boolean | null = null;

  if (error instanceof AxiosRequestPlusError) {
    isInternetAlive = error.isInternetAlive;
    error = error.originalError;
  }

  // Error local
  if (error instanceof DXTApiMalformedResponse) {
    const errorCode = DXTErrorCode.FATAL_MALFORMED_SERVER_RESPONSE;
    return {
      status: error.status,
      error: getDXTErrorDescription(errorCode),
      errorCode,
    };
  }

  if (error instanceof AxiosError) {
    const errorCode = (isInternetAlive ?? false)
      ? DXTErrorCode.INTERNAL_SERVER_ERROR
      : DXTErrorCode.CLIENT_CONNECTION_ERROR;

    return {
      status: error.response?.status ?? getHttpStatusFromErrorCode(errorCode),
      error: getDXTErrorDescription(errorCode),
      errorCode,
      extra: error.message,
    };
  }

  const errorCode = DXTErrorCode.UNEXPECTED_ERROR;

  return {
    errorCode,
    error: getDXTErrorDescription(errorCode),
    status: getHttpStatusFromErrorCode(errorCode),
    extra: error.message,
  };
}

export type DXTErrorCallback = (err: DXTErrorInfo) => void;

export enum DXTRetryPossibility {
  clientSideErrorRetryable,
  serverSideErrorRetryable,
  serverSideErrorNoRetryable,
}

export function getRetryPossibility(error: DXTErrorInfo): DXTRetryPossibility {
  const _RETRYABLE_CLIENT_SIDE_ERRORS: number[] = [
    DXTErrorCode.CLIENT_CONNECTION_ERROR,
    DXTErrorCode.INTERNAL_CLIENT_ERROR,
  ];

  const _RETRYABLE_SERVER_SIDE_ERRORS: number[] = [
    DXTErrorCode.CLIENT_CONNECTION_ERROR,
    DXTErrorCode.FATAL_MALFORMED_SERVER_RESPONSE,
    DXTErrorCode.SERVER_BUSY,
    DXTErrorCode.UNEXPECTED_ERROR,
    DXTErrorCode.INTERNAL_SERVER_ERROR,
  ];

  const genericErrorCode = getGenericErrorCode(error.errorCode);

  if (
    _RETRYABLE_CLIENT_SIDE_ERRORS.includes(error.errorCode) ||
    _RETRYABLE_CLIENT_SIDE_ERRORS.includes(genericErrorCode)
  ) {
    return DXTRetryPossibility.clientSideErrorRetryable;
  }

  if (
    _RETRYABLE_SERVER_SIDE_ERRORS.includes(error.errorCode) ||
    _RETRYABLE_SERVER_SIDE_ERRORS.includes(genericErrorCode)
  ) {
    return DXTRetryPossibility.serverSideErrorRetryable;
  }

  return DXTRetryPossibility.serverSideErrorNoRetryable;
}

enum ErrorNature {
  unwantedClientError, // Request technical assistance
  unwantedServerError, // Request technical assistance
  acceptableClientError,
  acceptableServerError,
}

export function getErrorNature(error: DXTErrorInfo): ErrorNature {
  const _ACCEPTABLE_CLIENT_SIDE_ERRORS: number[] = [
    DXTErrorCode.CLIENT_CONNECTION_ERROR,
  ];

  if (error.errorCode === DXTErrorCode.FATAL_MALFORMED_SERVER_RESPONSE) {
    return ErrorNature.unwantedServerError;
  }

  const genericErrorCode = getGenericErrorCode(error.errorCode);

  if (
    _ACCEPTABLE_CLIENT_SIDE_ERRORS.includes(error.errorCode) ||
    _ACCEPTABLE_CLIENT_SIDE_ERRORS.includes(genericErrorCode)
  )
    return ErrorNature.acceptableClientError;

  return ErrorNature.acceptableServerError;
}

export function doesErrorRequiresTechnicalAssistance(
  error: DXTErrorInfo
): boolean {
  const errorNature = getErrorNature(error);
  return (
    errorNature === ErrorNature.unwantedClientError ||
    errorNature === ErrorNature.unwantedServerError
  );
}

export function isNetworkError(error: DXTErrorInfo): boolean {
  const genericErrorCode = getGenericErrorCode(error.errorCode);

  return (
    genericErrorCode === DXTErrorCode.CLIENT_CONNECTION_ERROR ||
    genericErrorCode === DXTErrorCode.INTERNAL_SERVER_ERROR
  );
}


export function isDXTException(e: unknown, errorCode?: DXTErrorCode): e is DXTException {
  return e instanceof DXTException && (errorCode == null || e.dxtError.errorCode === errorCode || e.dxtError.genericErrorCode === errorCode);
}


export function isNotFoundException(e: unknown): e is DXTException  {
  return isDXTException(e, DXTErrorCode.NOT_FOUND);
}


export function isTangoException(e: unknown): e is DXTException  {
  return isDXTException(e, DXTErrorCode.TANGO_ERROR);
}
