const _ERRORS_REQUIRING_CONTACTING_ADMIN: DXTErrorCode[] = [
  DXTErrorCode.UNEXPECTED_ERROR,
  DXTErrorCode.NOT_IMPLEMENTED,
  DXTErrorCode.PAYLOAD_TOO_LARGE,
  DXTErrorCode.UNPROCESSABLE_ENTITY,
  DXTErrorCode.TANGO_ERROR,
  DXTErrorCode.INTERNAL_SERVER_ERROR,
];


export interface DXTErrorInfoBase {
  readonly errorCode: number;
}

export interface DXTErrorInfo extends DXTErrorInfoBase, ApiErrorResponse {
  status: number;
  error: string;
  extra?: string;
  isSilent?: boolean;
  message_to_user?: SystemMessageToUser;
}

export function getGenericErrorCode(errorCode: number) {
  return errorCode - (errorCode % 1000);
}

export function getDXTErrorDescription(errorCode: number): string {
  return _API_ERROR_MESSAGES.get(errorCode)?.toString() ?? errorCode.toString();
}

export function getHttpStatusFromErrorCode(errorCode: number): number {
  const generalErrorCode = getGenericErrorCode(errorCode);
  return _API_ERROR_HTTP_STATUS.get(generalErrorCode) ?? HttpStatus.INTERNAL_SERVER_ERROR;
}

export function getErrorCodeFromHttpStatus(status: number): number {
  return _HTTP_STATUS_TO_API_ERRORS.get(status) ?? DXTErrorCode.UNEXPECTED_ERROR;
}

export function requiresContactingAdmin(errorCode: number): boolean {
  return (
    _ERRORS_REQUIRING_CONTACTING_ADMIN.includes(errorCode)
    ||
    _ERRORS_REQUIRING_CONTACTING_ADMIN.includes(getGenericErrorCode(errorCode))
  );
}


export class DXTError implements DXTErrorInfo {
  readonly status: number;
  readonly error: string;
  readonly genericErrorCode: number;
  readonly extra?: string;
  readonly isSilent?: boolean;
  readonly messageToUser?: SystemMessageToUser;

  constructor(
    public readonly errorCode: number,
    options?: {
      isSilent?: boolean;
      extra?: string;
      messageToUser?: SystemMessageToUser
    }
  ) {
    this.genericErrorCode = getGenericErrorCode(errorCode);
    this.error = getDXTErrorDescription(errorCode);
    this.status = getHttpStatusFromErrorCode(this.errorCode);
    this.extra = options?.extra;
    this.isSilent = options?.isSilent;
    this.messageToUser = options?.messageToUser;
  }
}

export class DXTApiMalformedResponse extends Error {
  constructor(readonly status: number) {
    super();
  }
}

export class DXTException extends Error {
  readonly dxtError: DXTError;

  constructor(error: DXTError | number, customMessage?: string) {
    const dxtError = error instanceof DXTError ? error : new DXTError(error, { extra: customMessage });
    super(dxtError.error);
    this.name = this.constructor.name.toString();
    this.dxtError = dxtError;
  }

  isUnexpected(): boolean {
    return isHttpServerError(this.dxtError.status);
  }
}
