import { isRealObject } from "@/objects";

export function isBasicAppResources(app: any): boolean {
  return (
    app?.navigate != null &&
    app.location != null &&
    typeof app.retryCallback === 'function'
  );
}

export function isAppResources(app: any): boolean {
  return (
    isBasicAppResources(app) &&
    isRealObject(app?.authState) &&
    typeof app.authDispatch === 'function'
  );
}
