import validator from 'validator';

import { isRealObject } from '../objects';
import {
  InvalidEnumNumberException,
  InvalidEnumObjectException,
} from './errors';

export const MD5_LENGTH = 32;

export const INT8_BYTES = 1;
export const INT16_BYTES = 2;
export const INT32_BYTES = 4;
export const INT64_BYTES = 8;
export const INT128_BYTES = 16;
export const DATE_BYTES = INT64_BYTES;
export const UUID_BYTES = INT128_BYTES;

export const UINT8_MIN = 0;
export const UINT8_MAX = 2 ** (INT8_BYTES * 8) - 1;

export const INT16_MIN = -(2 ** (INT16_BYTES * 8 - 1));
export const INT16_MAX = 2 ** (INT16_BYTES * 8 - 1) - 1;

export const UINT16_MIN = 0;
export const UINT16_MAX = 2 ** (INT16_BYTES * 8) - 1;

export const UINT32_MIN = 0;
export const UINT32_MAX = 2 ** (INT32_BYTES * 8) - 1;

export const UINT64_MIN_VALUE = BigInt(0);
export const UINT64_MAX_VALUE = BigInt(`0x${'FF'.repeat(INT64_BYTES)}`); // BigInt('18446744073709551615');

export const UINT128_MIN_VALUE = BigInt(0);
export const UINT128_MAX_VALUE = BigInt(`0x${'FF'.repeat(INT128_BYTES)}`); // BigInt('340282366920938463463374607431768211455');

export const EMAIL_MAX_LENGTH = 512;

export const PHONE_MIN_LENGTH = 5;
export const PHONE_MAX_LENGTH = 50;

export const URL_MIN_LENGTH = 5;
export const URL_MAX_LENGTH = 2048;

export const TCP_PORT_MIN_VALUE = 1;
export const TCP_PORT_MAX_VALUE = 65535;

export const UI64_ENTITY_ID_BYTES = INT64_BYTES;
export const ENTITY_UUID_BYTES = UUID_BYTES;

export const UI64_ENTITY_ID_MIN_VALUE = UINT64_MIN_VALUE;
export const UI64_ENTITY_ID_MAX_VALUE = UINT64_MAX_VALUE;

export const ENTITY_UUID_MIN_VALUE = UINT128_MIN_VALUE;
export const ENTITY_UUID_MAX_VALUE = UINT128_MAX_VALUE;

export const IP_BYTES = 128 / 8;

export const NODE_ID_BYTES = INT16_BYTES;
export const NODE_ID_MIN_VALUE = 0;
export const NODE_ID_MAX_VALUE = 2 ** (NODE_ID_BYTES * 8) - 1;

export const NETWORK_ID_BYTES = UI64_ENTITY_ID_BYTES;

export const PROFILE_ID_BYTES = UI64_ENTITY_ID_BYTES;

export const ENTITY_NAME_MIN_LENGTH = 2;
export const ENTITY_NAME_MAX_LENGTH = 100;

export const SCREEN_NAME_MIN_LENGTH = 2;
export const SCREEN_NAME_MAX_LENGTH = 50;

export const POST_MIN_LENGTH = 4;
export const POST_MAX_LENGTH = 5000;

export const CUSTOM_GENDER_MIN_LENGTH = 4;
export const CUSTOM_GENDER_MAX_LENGTH = 100;

export const FIRST_NAME_MIN_LENGTH = 2;
export const FIRST_NAME_MAX_LENGTH = 60;

export const LAST_NAME_MIN_LENGTH = 2;
export const LAST_NAME_MAX_LENGTH = 60;

export const FULL_NAME_MIN_LENGTH = FIRST_NAME_MIN_LENGTH;
export const FULL_NAME_MAX_LENGTH =
  FIRST_NAME_MAX_LENGTH + LAST_NAME_MAX_LENGTH + 1;

export const CUSTOM_GENDER_NAME_MIN_LENGTH = 3;
export const CUSTOM_GENDER_NAME_MAX_LENGTH = 100;

export const LANGUAGE_LENGTH = 2;

export const COUNTRY_CODE_LENGTH = 2;

export interface TEnum<T> {
  [id: string]: T | string;
  [nu: number]: string;
}


export function isPhone(value: unknown): value is string {
  return (
    typeof value === 'string' &&
    value.length >= PHONE_MIN_LENGTH &&
    value.length <= PHONE_MAX_LENGTH
  );
}

export function isHost(value: unknown): value is string {
  return (
    typeof value === 'string' &&
    (
      validator.isFQDN(value, {}) ||
      validator.isIP(value) ||
      value === 'localhost'
    )
  );
}

export function isTCPPort(value: unknown): value is number {
  return (
    typeof value === 'number' &&
    value >= TCP_PORT_MIN_VALUE &&
    value <= TCP_PORT_MAX_VALUE
  );
}

export function isUrl(value: unknown): value is string {
  return (
    typeof value === 'string' &&
    value.length >= URL_MIN_LENGTH &&
    value.length <= URL_MAX_LENGTH
  );
}

export function isBoolean(value: unknown): value is boolean {
  if (typeof value === 'boolean') return true;

  if (typeof value === 'number') return true;

  if (typeof value === 'string') {
    value = value.trim().toLowerCase();

    switch (value) {
      case '0':
      case '1':
      case 'false':
      case 'true':
        return true;
    }
  }

  return false;
}

export function isEntity64Id(value: unknown): value is bigint {
  return (
    typeof value === 'bigint' &&
    value >= UI64_ENTITY_ID_MIN_VALUE &&
    value <= UI64_ENTITY_ID_MAX_VALUE
  );
}

export function isEntityUUId(value: unknown): value is bigint {
  return (
    typeof value === 'bigint' &&
    value >= ENTITY_UUID_MIN_VALUE &&
    value <= ENTITY_UUID_MAX_VALUE
  );
}

export function isEntityName(value: unknown): value is string {
  return (
    typeof value === 'string' &&
    value.length >= ENTITY_NAME_MIN_LENGTH &&
    value.length <= ENTITY_NAME_MAX_LENGTH
  );
}

export function isName(
  value: unknown,
  minLength: number,
  maxLength: number
): value is string {
  const re = /^[\p{L}\s]+$/iu;

  return (
    typeof value === 'string' &&
    value.length >= minLength &&
    value.length <= maxLength &&
    re.test(value)
  );
}

export function isScreenName(value: unknown): value is string {
  return isName(value, SCREEN_NAME_MIN_LENGTH, SCREEN_NAME_MAX_LENGTH);
}

export function isFirstname(value: unknown): value is string {
  return isName(value, FIRST_NAME_MIN_LENGTH, FIRST_NAME_MAX_LENGTH);
}

export function isLastname(value: unknown): value is string {
  return isName(value, LAST_NAME_MIN_LENGTH, LAST_NAME_MAX_LENGTH);
}

export function isFullName(value: unknown): value is string {
  return isName(value, FULL_NAME_MIN_LENGTH, FULL_NAME_MAX_LENGTH);
}

export function isLanguage(value: unknown): value is string {
  return (
    typeof value === 'string' &&
    value.length === LANGUAGE_LENGTH &&
    _isLowerCase(value[0]) &&
    _isLowerCase(value[1])
  );
}

export function isLocale(value: unknown): value is string {
  const re = /^[a-z]{2}-[A-Z]{2}$/iu;
  return typeof value === 'string' && re.test(value);
}

export function isCountryCode(value: unknown): value is string {
  return (
    typeof value === 'string' &&
    value.length === COUNTRY_CODE_LENGTH &&
    _isLowerCase(value[0]) &&
    _isLowerCase(value[1])
  );
}

export function _isLowerCase(char: unknown): char is string {
  return typeof char === 'string' && char[0] >= 'a' && char[0] <= 'z';
}

export function assertEnumType(enumType: unknown): asserts enumType is object {
  if (isRealObject(enumType)) return ;

  throw new InvalidEnumObjectException(enumType);
}

export function isValidEnumValue<E>(enumType: unknown, value: E): value is E {
  assertEnumType(enumType);

  if (typeof value !== 'number') throw new InvalidEnumNumberException(value);

  return typeof (enumType as any)[value] === 'string';
}
