import { axiosRequestPlus } from '@/axios/axiosRequestPlus';
import {
  type AxiosRequestConfig,
  type AxiosResponse,
  type AxiosResponseHeaders,
} from 'axios';
import { isBasicAppResources } from 'src/code.client/app_resources/utils';
import { dxtApiConfig } from 'src/config/dxtango_api';
import { type MinimalAppResources } from 'src/domain/shared/types';

import {
  DXTApiMalformedResponse,
  DXTError,
  type DXTErrorInfo,
  DXTException,
  HTTP_STATUS_UNAVAILABLE,
  DXTErrorCode,
} from '../../domain/shared/errors/base';

import {
  exceptionToDXTErrorInfo,
  isErrorInfo,
} from '../../domain/shared/errors/errors.utils';

import {
  type DXTApiRequestState,
  DXTApiRequestStateError,
  DXTApiRequestStateSuccess,
} from './DXTApiRequestState';
import { parseJSON } from '@core/json';

export interface DXTApiRequestConfig<D> extends AxiosRequestConfig<D> {
  pathParams?: Record<string, string>;
  silent?: boolean;
}

export function createDXTApiParams<D>(
  app?: MinimalAppResources,
  options?: DXTApiRequestConfig<D>
): AxiosRequestConfig<D> {
  const {
    headers: originalHeaders,
    transformResponse: originalTransformers,
    pathParams,
    url: originalUrl,
    ...remainingOptions
  } = options ?? {};

  let url = originalUrl;

  if (typeof url === 'string' && pathParams != null) {
    Object.entries(pathParams).forEach(([key, value]) => {
      url = url?.replaceAll(`:${key}`, encodeURIComponent(value));
    });
  }

  let newTransformers = [
    (data: string, headers: AxiosResponseHeaders) => {
      if (typeof data !== 'undefined') return parseJSON(data);
    },
  ];

  const token = app?.authToken;

  if (Array.isArray(originalTransformers)) {
    newTransformers.push(...originalTransformers);
  } else if (typeof originalTransformers === 'function') {
    newTransformers.push(originalTransformers);
  }

  return {
    headers: {
      ...((token != null) ? { authorization: token } : null),
      ...originalHeaders,
    },
    baseURL: dxtApiConfig.baseUrl,
    url,
    ...remainingOptions,
    transformResponse: newTransformers,
  };
}

export async function dxtApiRequest<T, D = undefined>(
  params: DXTApiRequestConfig<D>,
  app?: MinimalAppResources
): Promise<DXTApiRequestState<T, D>> {
  const dxtParams = createDXTApiParams(app, params);

  let retry = true;
  while (retry) {
    try {
      const response = await axiosRequestPlus<
        T,
        AxiosResponse<T, D>,
        D
      >(dxtParams);

      if (isErrorInfo(response?.data)) {
        const dxtApiError = response?.data as unknown as DXTErrorInfo;

        throw new DXTException(
          new DXTError(dxtApiError.errorCode, {
            extra: dxtApiError.extra,
            isSilent: dxtApiError.isSilent,
          })
        );
      }

      if (response?.data === undefined)
        throw new DXTApiMalformedResponse(
          response?.status ?? HTTP_STATUS_UNAVAILABLE
        );

      const data: T = response.data;

      return new DXTApiRequestStateSuccess(data, dxtParams);
    } catch (error) {
      const dxtApiError = exceptionToDXTErrorInfo(error);

      const errorState = new DXTApiRequestStateError<T, D>(
        dxtApiError,
        dxtParams
      );

      if (!app || !isBasicAppResources(app)) return errorState;

      if ((params?.silent) ?? false) return errorState;

      retry =
        typeof app.retryCallback === 'function' &&
        (await app.retryCallback({ message: dxtApiError.error }));

      if (!retry) return errorState;
    }
  }

  const impossibleError = exceptionToDXTErrorInfo(new DXTException(DXTErrorCode.INTERNAL_SERVER_ERROR, 'Impossible state'));

  const impossibleRequestState = new DXTApiRequestStateError<T, D>(
    impossibleError,
    dxtParams
  );

  return impossibleRequestState;
}
