import { inputAnatomy } from '@chakra-ui/anatomy';
import { createMultiStyleConfigHelpers } from '@chakra-ui/react';
import { getColorVar, mode } from '@chakra-ui/theme-tools';

const { definePartsStyle, defineMultiStyleConfig } =
  createMultiStyleConfigHelpers(inputAnatomy.keys);


const variantOutlineStyle = definePartsStyle((props) => {
  const { theme } = props

  const ec = mode("red.500", "red.300")(props);
  
  return {
    field: {
      _invalid: {
        borderColor: `${getColorVar(theme, ec)} !important`,
        boxShadow: `0 0 0 1px ${getColorVar(theme, ec)} !important`,
      },
    }
  }
});

const variants = {
  outline: variantOutlineStyle,
};

export const inputTheme = defineMultiStyleConfig({ variants });
