import { toBigIntBE, toBigIntLE, toBufferBE, toBufferLE } from 'bigint-buffer';
import validator from 'validator';

import {
  InvalidBooleanException,
  InvalidEntityUUIdException,
  InvalidEnumNumberException,
  InvalidEnumStringException,
  InvalidParamsException,
  InvalidUUIdBufferException,
} from '../validation/errors';
import { type EntityId } from '../value_objects/types';
import {
  isEntityUUId,
  type TEnum,
  UUID_BYTES,
  assertEnumType,
} from '../validation/base';

export function normalizeEmail(email: string): string {
  const normalizedEmailAttempt = validator.normalizeEmail(email, {
    gmail_remove_dots: true,
    gmail_remove_subaddress: true,
  });

  const normalizedEmail =
    normalizedEmailAttempt === false ? email : normalizedEmailAttempt;

  return normalizedEmail;
}

export function secureToBigIntBE(buf: Buffer): bigint {
  if (buf instanceof Buffer) return toBigIntBE(buf);

  throw new InvalidParamsException('secureToBigIntBE');
}


export function secureToBigIntLE(buf: Buffer): bigint {
  if (buf instanceof Buffer) return toBigIntLE(buf);

  throw new InvalidParamsException('secureToBigIntLE');
}


export function secureToBufferBE(num: bigint, width: number): Buffer {
  if (typeof num === 'bigint' && typeof width === 'number') toBufferBE(num, width);

  throw new InvalidParamsException('secureToBufferBE');
}


export function secureToBufferLE(num: bigint, width: number): Buffer {
  if (typeof num === 'bigint' && typeof width === 'number') toBufferLE(num, width);

  throw new InvalidParamsException('secureToBufferLE');
}


export function toBoolean(rawValue: unknown, def?: boolean): boolean {
  let value = rawValue;

  if (typeof value === 'boolean') return value;

  if (typeof value === 'number') return value !== 0;

  if (typeof value === 'string') {
    value = value.trim().toLowerCase().trim();
    if (value === '1' || value === 'true') return true;
    if (value === '0' || value === 'false') return false;
  }

  if (typeof def !== 'boolean') throw new InvalidBooleanException(rawValue);

  return def;
}

export function stringToEnum<E>(enumType: TEnum<E>, value: unknown): E {
  if (typeof value !== 'string') throw new InvalidEnumStringException(value);

  assertEnumType(enumType);
  const result = enumType[value];
  if (typeof result === 'undefined') throw new InvalidEnumNumberException(result);

  if (typeof result !== 'number') throw new InvalidEnumNumberException(result);

  return result as any;
}

export function enumToString<E>(enumType: TEnum<E>, value: E): string {
  if (typeof value !== 'number') throw new InvalidEnumNumberException(value);

  assertEnumType(enumType);
  
  const result = enumType[value];
  if (typeof result === 'undefined') throw new InvalidEnumStringException(result);
  
  if (typeof result !== 'string') throw new InvalidEnumStringException(result);

  return result;
}

export function uuidBufferToEntityId(value: Buffer): EntityId {
  if (value instanceof Buffer && value.length === UUID_BYTES) {
    return secureToBigIntBE(value);
  }

  throw new InvalidUUIdBufferException();
}

export function uuidEntityIdToBuffer(value: EntityId): Buffer {
  if (isEntityUUId(value)) {
    return secureToBufferBE(value, UUID_BYTES);
  }

  throw new InvalidEntityUUIdException(value);
}

export function uuidBufferToString(value: Buffer) {
  if (value instanceof Buffer && value.length === UUID_BYTES) {
    return value.toString('hex');
  }

  throw new InvalidUUIdBufferException();
}


type DefaultFormDataResult = Record<string, any>;

export function formDataToObject<T extends DefaultFormDataResult = DefaultFormDataResult>(formData: FormData): T {
  const formDataObj: Record<string, any> = {};

  for (const key of formData.keys()) {
    formDataObj[key] = formData.get(key);
  }

  return formDataObj as T;
}