import { ValidationException } from '@/validation/errors';
import { type VOInput, VOSHA1, VOUInt32, ValueObject, VOUInt16 } from '@/value_objects';

import { VOAuthRandom } from './vo_auth_random';
import { DXTErrorCode, DXTException } from 'src/domain/shared/errors';

export interface RightAuthTokenProps {
  readonly userId: VOUInt32;
  readonly roleNumber: VOUInt16;
}

export class AuthTokenProps {
  readonly userId: VOUInt32;
  readonly random: VOAuthRandom;
  readonly roleNumber: VOUInt16;

  constructor(
    userId: number,
    random: bigint,
    roleNumber: number,
  ) {
    this.userId = new VOUInt32(userId);
    this.random = new VOAuthRandom(random);
    this.roleNumber = new VOUInt16(roleNumber);
  }
}

export class VOWeakCheckAuthToken extends ValueObject<AuthTokenProps> {
  protected verificationHash?: VOSHA1;

  validate(rawValue: VOInput<AuthTokenProps>): AuthTokenProps {

    if (rawValue instanceof AuthTokenProps) {
      throw new ValidationException(
        'VOWeakCheckAuthToken does not accept AuthTokenProps as value'
      );
    }
    
    let value = rawValue;

    try {
      if (typeof value === 'string') {
        value = value.trim().toLowerCase();

        const parts = value.split('-');
        if (parts.length === 4) {
          const userId = Number.parseInt('0x' + parts[0]);
          const random = BigInt('0x' + parts[1]);
          const roleNumber = Number.parseInt(parts[2]);

          this.verificationHash = new VOSHA1(parts[3]);

          return new AuthTokenProps(
            userId,
            random,
            roleNumber,
          );
        }
      }
    } catch (e) {
      // console.error(e);
    }

    this.throwError(rawValue);
  }

  override rawValueToErrorString(rawValue: string | AuthTokenProps): string {
    return typeof rawValue === 'string' ? rawValue : JSON.stringify(rawValue);
  }

  protected getVerificationHashString(): string {
    if (!this.verificationHash) throw new DXTException(DXTErrorCode.UNEXPECTED_ERROR, 'VOWeakCheckAuthToken.verificationHash == [undefined]');

    return this.verificationHash.toString();
  }

  protected getPayloadString() {
    const userId = this.value.userId.valueOf().toString(16);
    const randomNumber = this.value.random.valueOf().toString(16);
    const roleNumber = this.value.roleNumber.valueOf();

    return `${userId}-${randomNumber}-${roleNumber}`;
  }

  private _payload: string | null = null;
  private _verificationHash: string | null = null;
  override toString(): string {
    this._payload ??= this.getPayloadString();
    this._verificationHash ??= this.getVerificationHashString();

    return `${this._payload}-${this._verificationHash}`;
  }
}
