export function isRealObject(value: unknown): value is Record<string, any> {
  return (
    typeof value === 'object' && !Array.isArray(value) && value !== null
  );
}


export function isObj(value: unknown): value is Record<string, any> {
  return (
    typeof value === 'object' && !Array.isArray(value) && value !== null
  );
}