import { ValidationException } from '../validation/errors';

export function random(min: number, max: number) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

const UINT64_VALUES = BigInt(1) << BigInt(64);
const INVALID_NUMBER_RANGE_ERROR = 'Invalid number range';

export function numberToUInt64(n: number): bigint {
  if (n < Number.MIN_SAFE_INTEGER || n > Number.MAX_SAFE_INTEGER)
    throw new ValidationException(INVALID_NUMBER_RANGE_ERROR);

  if (n < 0) {
    const absolute = Math.abs(n);
    return UINT64_VALUES - BigInt(absolute);
  } else {
    return BigInt(n);
  }
}

export function uint64ToNumber(bi: bigint): number {
  const result = BigInt.asIntN(64, bi);
  if (result < Number.MIN_SAFE_INTEGER || result > Number.MAX_SAFE_INTEGER)
    throw new ValidationException(INVALID_NUMBER_RANGE_ERROR);
  return Number(result);
}

export function getMinMaxIntegerOfEnum(e: object) {
  const values = Object.keys(e)
    .map((k) => Number.parseInt(k))
    .filter((k) => !isNaN(k));
  return {
    min: Math.min(...values),
    max: Math.max(...values),
  };
}

export function limitNumber(value: number, min?: number, max?: number) {
  min = min ?? Number.NEGATIVE_INFINITY;
  max = max ?? Number.POSITIVE_INFINITY;

  if (min > max) {
    const aux = min;
    min = max;
    max = min;
  }

  if (value < min) return min;
  if (value > max) return max;

  return value;
}
