import { join } from "path";
import { URL_ROOT_PATH } from "src/config/url_paths";
import { UserMinimalInfo } from "src/domain/user/entities/user";
import { NO_DATE } from "src/texts/common";

export const dateToLocale = (value: Date|string|undefined): string => {
  if (value==null) return NO_DATE;

  const date = value instanceof Date ? value : new Date(value);

  return date.toLocaleDateString('es-AR', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
  });
};

export function appPath(path: string) {
  return join(URL_ROOT_PATH, path);
}

export function resolveUserStatusColor(user:UserMinimalInfo): string {
  const { habilitado_en_dxt, habilitado_en_tango, usuario_tango_existe } = user;

  return (
    !habilitado_en_dxt ? 'grey.500' :
    habilitado_en_dxt && !usuario_tango_existe ? 'red.400' :
    habilitado_en_dxt && usuario_tango_existe && habilitado_en_tango === false ? 'yellow.400' :
    'green.400'
  );
}

export function pathParamsToUrl(url: string, pathParams: Record<string, string | number> | null) {
  if (typeof url === 'string' && pathParams != null) {
    Object.entries(pathParams).forEach(([key, value]) => {
      url = url?.replaceAll(`:${key}`, encodeURIComponent(value));
    });
  }
  
  return url;
}