import { isNavigator } from '@/environemnt';
import md5 from 'md5';

const _host = isNavigator() ? window.location.host : '';
const _storeKeyHash = md5(_host);

export function getStorageKey(base: string) {
  return `${base}_${_storeKeyHash}`;
}

export function getStorage(): Storage | null {
  if (isNavigator()) return window.localStorage;

  return null;
}
