import { VOWeakCheckAuthToken } from 'src/domain/auth/value_objects';
import { type DXTErrorInfo } from 'src/domain/shared/errors';

import { deleteAuthStoredData, setStoredToken, setStoredUserInfo } from '../auth_store.utils';
import { type UserPublicInfo } from 'src/domain/user/entities/user';

type AuthStateMapFunction<S extends AuthState, R> = (state: S) => R;

export abstract class AuthState {
  mapOrElse<R>(options: {
    loading?: AuthStateMapFunction<AuthStateLoading, R>;
    changingToken?: AuthStateMapFunction<AuthStateChangingToken, R>;
    loggedIn?: AuthStateMapFunction<AuthStateLoggedIn, R>;
    disconnected?: AuthStateMapFunction<AuthStateDisconnected, R>;
    orElse: AuthStateMapFunction<AuthState, R>;
  }): R {
    const { changingToken, loading, loggedIn, disconnected, orElse } = options;

    if (this instanceof AuthStateLoading && loading) return loading(this);
    if (this instanceof AuthStateChangingToken && changingToken) return changingToken(this);
    if (this instanceof AuthStateLoggedIn && loggedIn) return loggedIn(this);
    if (this instanceof AuthStateDisconnected && disconnected) return disconnected(this);

    return orElse(this);
  }

  mapReadyOrNot<R>(options: {
    ready: AuthStateMapFunction<AuthStateLoggedIn | AuthStateDisconnected, R>;
    notReady: AuthStateMapFunction<AuthState, R>;
  }): R {
    const { ready, notReady } = options;

    if (
      this instanceof AuthStateLoggedIn
      ||
      this instanceof AuthStateDisconnected
    ) {
      return ready(this);
    }

    return notReady(this);
  }

  map<R>(options: {
    loading: AuthStateMapFunction<AuthStateLoading, R>;
    changingToken: AuthStateMapFunction<AuthStateChangingToken, R>;
    loggedIn: AuthStateMapFunction<AuthStateLoggedIn, R>;
    disconnected: AuthStateMapFunction<AuthStateDisconnected, R>;
    orElse: AuthStateMapFunction<AuthState, R>;
  }): R {
    const { changingToken, loading, loggedIn, disconnected } = options;

    return this.mapOrElse({
      loading,
      changingToken,
      loggedIn,
      disconnected,
      orElse: (_) => {
        throw Error('Invalid AuthState');
      },
    });
  }

  public isLoading(): boolean {
    return this instanceof AuthStateLoading;
  }

  public isChangingToken(): boolean {
    return this instanceof AuthStateChangingToken;
  }

  public isLoggedIn(): boolean {
    return this instanceof AuthStateLoggedIn;
  }

  public isDisconnected(): boolean {
    return this instanceof AuthStateDisconnected;
  }

  public isDisconnectedAndNotRedirecting(): boolean {
    return this instanceof AuthStateDisconnected && !this.isRedirecting;
  }

  public userOrNull(): UserPublicInfo | null {
    return this instanceof AuthStateLoggedIn ? this.userInfo : null;
  }

  public authTokenOrNull(): VOWeakCheckAuthToken | null {
    if (this instanceof AuthStateLoggedIn) {
      try {
        return new VOWeakCheckAuthToken(this.authToken);
      } catch (_) {}
    }

    return null;
  }

  public authTokenString(): string | undefined {
    // return (this instanceof AuthStateLoggedIn) ? this.authToken : undefined;
    return this.authTokenOrNull()?.toString();
  }

  public errorOrNull(): DXTErrorInfo | null {
    if (this instanceof AuthStateDisconnected)
      return (this as AuthStateDisconnected).error ?? null;

    return null;
  }

  public nonSilentErrorOrNull(): DXTErrorInfo | null {
    const isDisconnected = this instanceof AuthStateDisconnected;
    const silent =
      isDisconnected &&
      typeof this.error?.isSilent === 'boolean' &&
      this.error?.isSilent;

    if (isDisconnected && !silent) return this.error ?? null;

    return null;
  }

}

export class AuthStateDisconnected extends AuthState {
  constructor(
    public readonly error?: DXTErrorInfo,
    public readonly isRedirecting: boolean = false
  ) {
    super();
    deleteAuthStoredData();
  }
}

export class AuthStateChangingToken extends AuthState {
  constructor(public readonly newToken: string) {
    super();
  }
}

export class AuthStateLoading extends AuthState {
}

export class AuthStateLoggedIn extends AuthState {
  constructor(
    public readonly authToken: string,
    public readonly userInfo: UserPublicInfo,
  ) {
    super();
    setStoredToken(authToken);
    setStoredUserInfo(userInfo);
  }
}
