import { type Dispatch, type SetStateAction } from 'react';

import { type DXTErrorCallback } from 'src/domain/shared/errors';
import { userAuthConnectRequest } from 'src/api-client/auth';

import { type BasicAppResources } from '../../../app_resources/basic_app_resources';

import {
  type AuthState,
  AuthStateDisconnected,
  AuthStateLoading,
  AuthStateLoggedIn,
} from '../states';
import { AuthAction, type OnRetryCallBack } from './base';

export class AuthActionChangeToken extends AuthAction {
  constructor(
    readonly newToken: string,
    readonly tokenChangedFromApp: boolean,
    readonly errorCallback?: DXTErrorCallback
  ) {
    super();
  }

  override async run(
    authState: AuthState,
    setAuthState: Dispatch<SetStateAction<AuthState>>,
    app: BasicAppResources,
    onRetryCallback: OnRetryCallBack | undefined | null
  ): Promise<void> {
    setAuthState(new AuthStateLoading());

    const { newToken } = this;

    const response = await userAuthConnectRequest(newToken, app, true);

    response.map({
      success: (stateSuccess) => {
        const userInfo = stateSuccess.data.user;

        setAuthState(
          new AuthStateLoggedIn(newToken, userInfo)
        );
      },
      error: (errorState) => {
        const apiError = errorState.error;

        setAuthState(new AuthStateDisconnected(apiError));

        if (this.errorCallback) this.errorCallback(apiError);
      },
    });
  }
}
