import { isNode } from "@/environemnt";

const DEFAULT_HOSTNAME = 'localhost';
const DEFAULT_PORT = '3000';
const DEFAULT_BASE_URL = `http://${DEFAULT_HOSTNAME}:${DEFAULT_PORT}`;

const baseUrl =
  (isNode()
    ? `http://${process.env.HOST ?? DEFAULT_HOSTNAME}:${process.env.PORT ?? DEFAULT_PORT}`
    : window?.location?.origin) ?? DEFAULT_BASE_URL;

export const dxtApiConfig = {
  baseUrl,
};
