import { isRealObject } from "../objects";

export type JsonObject = { [Key in string]?: JsonValue };
export interface JsonArray extends Array<JsonValue> {}
export type JsonValue =
  | string
  | number
  | boolean
  | JsonObject
  | JsonArray
  | null;


let _jsonPolyfillsInitialized = false;
export function jsonPolyfills(): void {
  if (_jsonPolyfillsInitialized) return;
  (BigInt as any).prototype.toJSON = function () {
    return this.toString();
  };
  _jsonPolyfillsInitialized = true;
}

jsonPolyfills();

function isArray(x: any): boolean {
  return Array.isArray(x);
}

function isString(x: any): boolean {
  return typeof x === 'string';
}

function isBufferLike(x: any): boolean {
  return (
    isRealObject(x) && x.type === 'Buffer' && (isArray(x.data) || isString(x.data))
  );
}

export function stringifyJSON(
  value: any,
  replacer?: (this: any, key: string, value: any) => any,
  space?: string | number
): string {
  return JSON.stringify(value, replacer, space);
}

export function parseJSON(
  text: string,
  reviver?: (this: any, key: string, value: any) => any
): any {
  return JSON.parse(text, reviver);
}

export function enhancedStringifyJSON(
  value: any,
  space?: string | number
): string {
  return EnhancedJSON.stringify(value, space);
}

export function enhancedParseJSON(text: string): any {
  return EnhancedJSON.parse(text);
}

// eslint-disable-next-line @typescript-eslint/no-extraneous-class
export class EnhancedJSON {

  static replacer(key: string, value: any): string {
    if (!isBufferLike(value)) return value;

    if (!isArray(value.data)) return value;

    if (value?.data?.length > 0) {
      value.data = 'base64:' + Buffer.from(value.data as ArrayBuffer).toString('base64');
    } else {
      value.data = '';
    }

    return value;
  }

  static reviver(key: string, value: any): Buffer {
    if (!isBufferLike(value)) return value;

    if (isArray(value.data)) return Buffer.from(value.data as ArrayBuffer);

    if (!isString(value.data)) return value;

    if ((value.data as string).startsWith('base64:')) {
      return Buffer.from(value.data.slice('base64:'.length) as string, 'base64');
    }

    // Assume that the string is UTF-8 encoded (or empty).
    return Buffer.from(value.data as string);
  }

  static stringify(value: any, space?: string | number): string {
    // eslint-disable-next-line @typescript-eslint/unbound-method
    return JSON.stringify(value, EnhancedJSON.replacer, space);
  }

  static parse(text: string): any {
    // eslint-disable-next-line @typescript-eslint/unbound-method
    return JSON.parse(text, EnhancedJSON.reviver);
  }
}
