import { valueToString } from '../strings';
import { isRealObject } from '../objects';


export function isErrorObject(value: unknown): value is Error {
  return (
    isRealObject(value) && 'message' in value && 'name' in value
  );
}


export function errorToString(err: any): string {
  if (isErrorObject(err)) return err.message;

  return valueToString(err);
}


export class ValidationException extends Error {
  constructor(
    message: string,
  ) {
    super(message);
    this.name = 'ValidationException';
  }
}

export class BufferExpectedException extends ValidationException {
  constructor() {
    super('Value must be a buffer');
  }
}

export class StringExpectedException extends ValidationException {
  constructor(value: any) {
    super('Value must be a string');
  }
}

export class InvalidUUIdBufferException extends ValidationException {
  constructor() {
    super('Invalid UUID buffer');
  }
}

export class InvalidEntityUUIdException extends ValidationException {
  constructor(value: any) {
    super(`Invalid Entity UUID: ${value.toString()}`);
  }
}

export class InvalidEnumObjectException extends ValidationException {
  constructor(value: any) {
    super(`Invalid enum object:  ${valueToString(value)}`);
  }
}

export class InvalidEnumNumberException extends ValidationException {
  constructor(value: any) {
    super(`Invalid enum number:  ${valueToString(value)}`);
  }
}

export class InvalidEnumStringException extends ValidationException {
  constructor(value: any) {
    super(`Invalid enum string: ${valueToString(value)}`);
  }
}

export class NumberExpectedException extends ValidationException {
  constructor(value: any) {
    super(`Value must be a number: ${valueToString(value)}`);
  }
}

export class InvalidBooleanException extends ValidationException {
  constructor(value: any) {
    super(`Invalid boolean: ${valueToString(value)}`);
  }
}

export class InvalidParamsException extends ValidationException {
  constructor(functionName: string) {
    super(`Invalid ${functionName} params`);
  }
}
