// ** React Imports
import {
  createContext,
  type PropsWithChildren,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';

import _ from 'lodash';
import { URL_AUTH_LOGIN_PATH } from 'src/config/url_paths';
import { eventBus } from 'src/event_bus';

import { useBasicAppResources } from '../../app_resources/basic_app_resources';
import { loggedInRedirect } from '../utils';
import { AuthActionChangeToken } from './actions';
import { type AuthAction, type OnRetryCallBack } from './actions/base';
import {
  getStoredToken,
  getStoredUserInfo,
  useTokenExistenceCheck,
} from './auth_store.utils';
import {
  type AuthState,
  AuthStateChangingToken,
  AuthStateDisconnected,
  AuthStateLoggedIn,
} from './states';

// import useSessionStorageState from 'use-session-storage-state'

export type AuthDispatch = (action: AuthAction) => Promise<void>;

export interface AuthContextValues {
  state: AuthState;
  dispatch: AuthDispatch;
  retry: () => Promise<void>;
}

export const AUTH_NEW_TOKEN_EVENT = 'auth_new_token';

// ** Defaults
const defaultValues: AuthContextValues = {
  state: _resolveState(),
  dispatch: async (action: AuthAction) => {
    await Promise.resolve();
  },
  retry: async () => {
    await Promise.resolve();
  },
};

const AuthContext = createContext(defaultValues);

function _resolveState() {
  if (typeof window === 'undefined') return new AuthStateDisconnected();

  const queryparams = new URLSearchParams(window.location.search);

  const storedToken = getStoredToken();

  const newToken = queryparams.get('at') ?? null;
  if (newToken != null && newToken !== storedToken) {
    // prioritize token sent by the app
    return new AuthStateChangingToken(newToken);
  }

  if (storedToken == null) {
    return new AuthStateDisconnected();
  }

  const forceRefresh = queryparams.get('force_refresh') === '1' ?? false;

  if (forceRefresh) {
    return new AuthStateChangingToken(storedToken);
  }

  const userInfo = getStoredUserInfo();

  if (userInfo) return new AuthStateLoggedIn(storedToken, userInfo);

  return new AuthStateChangingToken(storedToken);
}

export interface AuthProviderProps extends PropsWithChildren {
  onRetry?: OnRetryCallBack;
}

const AuthProvider = ({ children, onRetry }: AuthProviderProps) => {
  // ** States
  const [authState, setAuthState] = useState<AuthState>(_resolveState());

  const lastAction = useRef<AuthAction | null>(null);

  const app = useBasicAppResources(authState);

  // save appRef to have an updated version on the dispatch function
  const appRef = useRef(app);

  const forceLogoutAlreadyVerified = useRef(false);

  // ** Hooks
  const tokenExistence = useTokenExistenceCheck();

  const dispatch = async (action: AuthAction) => {
    lastAction.current = action;
    await action.run(authState, setAuthState, appRef.current, onRetry);
  };

  eventBus.removeAllListeners(AUTH_NEW_TOKEN_EVENT);
  eventBus.on(AUTH_NEW_TOKEN_EVENT, (newToken: string) => {
    (async () => {
      await dispatch(new AuthActionChangeToken(newToken, true));
    })().catch((e) => {});
  });

  // update app with dispatch for future use on BasicAppResources
  appRef.current = useBasicAppResources(authState, dispatch);

  const retry = async () => {
    if (lastAction.current)
      await lastAction.current.run(authState, setAuthState, app, onRetry);
  };

  // resolve state when token existance changes on window storage
  useEffect(() => {
    // console.log("TOKEN EXISTENCE: "+tokenExistence)

    // hack: storage calls are synchronous, use a small timeout to wait
    setTimeout(() => {
      if (authState instanceof AuthStateChangingToken) return;

      const newState = _resolveState();

      if (_.isEqual(newState, authState)) return;

      setAuthState(newState);
    }, 250);
  }, [tokenExistence]);

  useEffect(() => {
    if (authState instanceof AuthStateChangingToken) {
      (async () => {
        await dispatch(new AuthActionChangeToken(authState.newToken, false));
      })().catch((e) => {});
    }
  }, []);

  useEffect(() => {
    // redirect user if logged in and into any of the following pages
    if (app.location.pathname === URL_AUTH_LOGIN_PATH) {
      loggedInRedirect(app);
    }
  }, [authState, app.location]);

  const values = useMemo<AuthContextValues>(
    () => ({
      state: authState,
      dispatch,
      retry,
    }),
    [authState]
  );

  return <AuthContext.Provider value={values}>{children}</AuthContext.Provider>;
};

export { AuthContext, AuthProvider };

export const useAuth = () => useContext(AuthContext);
