import { type NavigateFunction } from '@remix-run/react';

import { HttpStatus } from '@/http';
import { URL_MAIN_PATH, URL_ROOT_PATH } from 'src/config/url_paths';

import { type BasicAppResources } from '../app_resources/basic_app_resources';
import { AuthActionLogout, AuthStateLoggedIn } from './auth_context';

export function isTokenError(errorCode: number): boolean {
  return (
    errorCode === HttpStatus.UNAUTHORIZED || errorCode === HttpStatus.FORBIDDEN
  );
}

export const checkTokenErrorAndRedirect = async (
  app: BasicAppResources,
  status: number
) => {
  const forceLogin = isTokenError(status);

  if (!forceLogin) return false;

  if (app.authState && app.authState instanceof AuthStateLoggedIn) {
    await app.authDispatch?.call(this, new AuthActionLogout(true));
  }

  redirectLoginWithReturnUrl(app.navigate);

  return true;
};

export function redirectLoginWithReturnUrl(navigate: NavigateFunction) {
  let returnUrl: string | null;

  if (window?.location != null) {
    const { pathname, search, hash } = window?.location;
    returnUrl = `${pathname}${search}${hash}`;
  } else {
    returnUrl = null;
  }

  const newSearchParams = (returnUrl != null)
    ? `?${new URLSearchParams([['returnUrl', returnUrl]]).toString()}`
    : '';

  navigate(`${URL_ROOT_PATH}${newSearchParams}`);
}

export function loggedInRedirect(app: BasicAppResources) {
  if ( app.authState instanceof AuthStateLoggedIn ) {
    const searchParams = new URLSearchParams(app.location.search);
    
    const returnUrl = searchParams.get('returnUrl');
    
    const redirectURL = (returnUrl != null) && returnUrl !== '/' ? returnUrl : URL_MAIN_PATH;

    app.navigate(redirectURL, {
      replace: true,
    });
  }
}
